import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    shouldQequest:true
  },
  mutations: {
    requsetCancel(state){
      state.shouldQequest = false
    },
    requestSure(state){
      state.shouldQequest = true
    }
  },
  actions: {
  },
  modules: {
  }
})
