import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "@/views/login/login.vue"
import Logon from "@/views/logon/logon.vue"
import Loging from "@/views/loging/index.vue"
import MainArea from "@/views/mainArea/index.vue"
import NotFund from "@/views/404/404.vue"
import StudentList from "@/views/mainArea/studentList.vue"
import AddStudent from "@/views/mainArea/addStudent.vue"

Vue.use(VueRouter)

const routes = [{
    path: "/",
    redirect: "/loging"
  },
  {
    path: '/loging',
    component: Loging,
    redirect: '/login',
    children: [
      {
        path: "/login",
        component: Login
      },
      {
        path: "/logon",
        component: Logon
      }
    ]
  },
  {
    path: "/mainArea",
    component: MainArea,
    redirect: "/studentList",
    children: [
      {
        path: "/studentList",
        component: StudentList
      },
      {
        path: "/addStudent",
        component: AddStudent
      }
    ]
  }, {
    path: "/404",
    component: NotFund
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.path == '/') {
    console.log(`login`);
    next("/login");
    return
  }
  setTimeout(() => {
    let a = window.sessionStorage.getItem("logoning");
    // console.log(a, to.path);
    if(to.path === "/logon"){
      next();
      return
    }
    if (!a && to.path !== '/login') {
      // 没有登录状态
      next('/');
      return
    } else if (to.matched.length == 0) {
      // 已登录
      // 进入的页面是否正确
      next("/404");
      return
    }
  }, 0)
  next()
})

export default router