import addressObj from "./address"
import axios from "axios"

const appkey = "yzxollie_1606813819603";

let instance = axios.create({
  baseURL: addressObj.baseURL,
  timeout: 5000,
  params: {
    appkey
  }
})

instance.interceptors.response.use(resp => {
  const data = resp.data;
  if (data.status === 'success') {
    return resp.data
  } else {
    return Promise.reject(data.msg);
  }
}, data => {
  return Promise.reject(data)
})

let ajax = {
  login: function (data) {
    return instance.post(addressObj.login, data)
  },
  logon: function (data) {
    let config = '';
    for (let i in data) {
      config += `&${i}=${data[i]}`
    };
    return instance.post(addressObj.logon, `appkey=${appkey}${config}`)
  },
  getStudentList() {
    return instance.get(addressObj.studentList);
  },
  searchStudent(search) {
    return instance.get(addressObj.searchStudent, {
      params: {
        sex: -1,
        search,
        page: 1,
        size: 1
      }
    })
  },
  deleteStudent(sNo){
    return instance.get(addressObj.deleteStudent,{
      params:{
        sNo
      }
    })
  },
  editStudent(data){
    return instance.get(addressObj.editStudent,{
      params:{
        ...data
      }
    })
  },
  addStudent(data){
    return instance.get(addressObj.addStudent,{
      params:data
    })
  }
}

export default ajax