import Vue from "vue";
import MessageModule from "./index.vue";
const MessageBox = Vue.extend(MessageModule);

export default function (message = "是否继续？") {
  return new Promise((resolve, reject) => {
    const app = new MessageBox({
      el: document.createElement("div"),
      data() {
        return {
          show: true,
          message
        }
      },
      methods: {
        cancle() {
          this.show = false;
          document.body.removeChild(app.$el)
          reject()
        },
        handleClick() {
          this.show = false;
          document.body.removeChild(app.$el)
          resolve()
        },
      }
    });
    document.body.appendChild(app.$el)
  })
}