<template>
  <div>
    <div class="username formitem">
      <div>
        <label for="username">用户名：</label>
        <input
          type="text"
          id="username"
          placeholder="用户名"
          v-model="account"
        />
      </div>
    </div>
    <div class="password formitem">
      <div>
        <label for="password">密码：</label>
        <input
          type="text"
          id="password"
          placeholder="密码"
          v-model="password"
          @keyup.enter="login"
        />
      </div>
    </div>
    <div class="toregistor">
      <router-link to="/logon" tag="a">没有账号？去注册</router-link>
    </div>
    <div class="btns">
      <button @click.prevent="reset">重置</button>
      <button @click.prevent="login">登录</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      account: "",
      password: "",
    };
  },
  methods: {
    async login() {
      try {
        const res = await this.$ajax.login(
          `account=${this.account}&password=${this.password}`
        );
        this.$router.push("/mainArea");
        window.sessionStorage.setItem("logoning", res.status);
        this.$toast({
          message: "登录成功",
          type: "success",
          duration: 2000,
        });
      } catch (err) {
        this.$toast({
          message: err,
          type: "info",
          duration: 1500,
        });
      }
    },
    reset() {
      this.account = "";
      this.password = "";
    },
  },
};
</script>

<style scoped>
@import "~@/assets/style/loginCommon.css";

.password {
  margin-bottom: 30px;
}

.toregistor {
  padding-left: 50px;
  font-size: 14px;
}
.toregistor a {
  color: #fff;
}
</style>