<template>
  <div>
    <table border="0" cellspacing="0" cellpadding="0">
      <thead>
        <tr>
          <th>学号</th>
          <th>姓名</th>
          <th>性别</th>
          <th>邮箱</th>
          <th>年龄</th>
          <th>手机号</th>
          <th>住址</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="(student, studentIndex) in list" 
          :key="student.id"
        >
          <td>{{ student.sNo }}</td>
          <td>{{ student.name }}</td>
          <td>{{ student.sex==0?"男":"女" }}</td>
          <td>{{ student.email }}</td>
          <td>{{ year - student.birth }}</td>
          <td>{{ student.phone }}</td>
          <td>{{ student.address }}</td>
          <td>
            <button class="btn edit" @click="editStudent(studentIndex)">编辑</button>
            <button class="btn del" @click="deleteStudent(student.sNo)">删除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <transition>
      <editFrame 
        @closeEdit="closeEdit" 
        :student="singleStudent" 
        v-if="editDisplay" 
        @submitChange="submit"
      />
    </transition>
  </div>
</template>

<script>
import editFrame from "@/components/editStudent";
export default {
  name: "studentTable",
  components:{
    editFrame
  },
  props:{
    list:{
      type:[Array,Object],
      default:[]
    },
    year:{
      default:new Date().getFullYear()
    }
  },
  computed:{
    studentList(){
      return this.list;
    }
  },
  data() {
    return {
      editDisplay:false,
      singleStudent:{}
    }
  },
  created() {  
  },
  methods: {
    async deleteStudent(sNo){
      const isDelete = window.confirm("是否删除该学生信息？");
      if(!isDelete) return;
      const res = await this.$ajax.deleteStudent(sNo);
      this.$emit("successDel");
      alert(res.msg);
    },
    editStudent(index){
      this.editDisplay = true;
      this.singleStudent = this.list[index];
    },
    closeEdit(){
      this.editDisplay = false
    },
    async submit($event){
      let res = await this.$ajax.editStudent($event);
      if(res.status === 'success'){
        this.$toast({
          message:"修改成功",
          type:"success",
        });
        this.editDisplay = false;
        this.$parent.getStudents()
      }
    }
  },
};
</script>

<style scoped>
table {
  width: 100%;
}
table tr {
  height: 40px;
  line-height: 40px;
  background-color: #fff;
  text-align: center;
}
table thead tr {
  font-size: 15px;
  background-color: #ccc;
  letter-spacing: 0.1em;
  text-align: center;
}
table tbody tr{
  font-size: 12px;
}
table tbody tr button {
  cursor: pointer;
  width: 60px;
  height: 30px;
  border: none;
  outline: none;
  background-color: #006400;
  overflow: hidden;
  color: #fff;
  border-radius: 5px;
  font-weight: 500px;
}
table tbody tr button.del {
  background-color: #cd0000;
  margin-left: 8px;
}
.v-enter,
.v-leave-to{
  transform: translateY(-100vh) rotateZ(45deg) scale(1.2);
  opacity: 0;
}
.v-enter-active,
.v-leave-active{
  transition: all .4s;
}
.v-enter-to,
.v-leave{
  transform: translateY(0) rotateZ(0) scale(1);
}
</style>
