import Vue from "vue";
import ToastModule from "./index.vue";

const Toast = Vue.extend(ToastModule);

export default function ({
  message,
  type = "info",
  duration = 2000
}) {
  const app = new Toast({
    el: document.createElement('div'),
    data() {
      return {
        message,
        type,
        duration
      }
    },
  });
  duration = duration < 1500 ? 1500 : duration;
  document.body.appendChild(app.$el);
  setTimeout(() => {
    app.flag = false;
  }, duration - 500);
  setTimeout(() => {
    document.body.removeChild(app.$el);
  }, duration)
}