<template>
  <div>
    <div class="student-list-search">
      <label for="student-num">搜索：</label>
      <input
        type="text"
        id="student-num"
        @keypress.enter="handleClick"
        v-model="search"
        placeholder="请输入学号"
      />
      <button class="search-btn" @click="handleClick">搜索</button>
    </div>
    <DataTable :list="studentList" @successDel="successDel" />
  </div>
</template>

<script>
import { mapState } from "vuex";
import DataTable from "@/components/studentTable.vue";
export default {
  name: "studentList",
  components: {
    DataTable,
  },
  data() {
    return {
      studentList: [],
      search: "",
    };
  },
  computed: {
    ...mapState(["shouldQequest"]),
  },
  created() {
    this.getStudents();
  },
  activated() {
    if (this.shouldQequest || this.studentList.length === 0) {
      this.getStudents();
      this.$store.commit("requsetCancel");
    }
  },
  methods: {
    getStudents() {
      this.$ajax.getStudentList().then((res) => {
        this.studentList = res.data;
      });
    },
    async handleClick() {
      let {
        data: { searchList: res },
      } = await this.$ajax.searchStudent(this.search);
      this.studentList = res;
    },
    successDel() {
      this.getStudents();
    },
  },
};
</script>

<style scoped>
.student-list-search {
  height: 50px;
  display: flex;
  align-items: center;
  margin: 15px 0;
}
.student-list-search label {
  width: 50px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  font-weight: 500;
}
.student-list-search input {
  padding: 10px;
  border: 1px solid #bbb;
  border-radius: 5px;
  margin-right: 10px;
  outline: none;
  background-color: #eee;
}
.student-list-search .search-btn {
  margin-left: 10px;
  width: 60px;
  height: 28px;
  border: none;
  outline: none;
  color: #fff;
  background-color: #333;
  border-radius: 6px;
}
.student-list-search .search-btn:active {
  box-shadow: inset 1px 1px 1px #ccc, inset -1px -1px 1px #ccc, 0 0 5px #000;
}
</style>
