<template>
  <div class="message" :class="[type, animateClass]">
    <i class="iconfont" :class="[iconClass]"></i>
    <span class="msg" style="marginLeft: 15px">{{ message }}</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: true,
    };
  },
  computed: {
    iconClass() {
      let commonBegin = "icon-";
      if (this.type == "success") {
        commonBegin += "Messagesuccess";
      }
      if (this.type == "warn") {
        commonBegin += "ware";
      }
      if (this.type == "error") {
        commonBegin += "deletorerror";
      }
      if (this.type == "info") {
        commonBegin += "icon-info";
      }
      return commonBegin;
    },
    animateClass() {
      return this.flag ? "fadeIn" : "fadeOut";
    },
  },
};
</script>

<style scoped>
@import "~@/assets/font/iconfont.css";
.message {
  position: fixed;
  min-width: 200px;
  top: 30px;
  left: 50%;
  padding: 5px 15px;
  line-height: 30px;
  transform: translateX(-50%);
  border-radius: 4px;
  font-size: 14px;
  display: flex;
  align-items: center;
  user-select: none;
  z-index: 99;
}
.success {
  background-color: #f0f9eb;
  color: #67c23a;
}

.warn {
  background-color: #fdf6ec;
  color: #e6a23c;
}

.info {
  background-color: #f4f4f5;
  color: #909399;
}

.error {
  background-color: #fef0f0;
  color: #f56c6c;
}

@keyframes fadeIn {
  from {
    top: 0;
    opacity: 0;
  }

  to {
    top: 30px;
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    top: 30px;
    opacity: 1;
  }

  to {
    top: 0;
    opacity: 0;
  }
}

.fadeIn {
  animation: fadeIn 0.5s linear forwards;
}

.fadeOut {
  animation: fadeOut 0.5s linear forwards;
}
</style>