<template>
  <div class="addForm">
    <div class="title">
      <span>添加学生</span>
    </div>
    <div class="formArea">
      <div class="input-item" v-myblur>
        <input type="text" placeholder="学号" v-model="studentObj.sNo" />
        <p class="info">请输入4-16位的学号</p>
      </div>
      <div class="input-item" v-myblur>
        <input type="text" placeholder="姓名" v-model="studentObj.name" />
        <p class="info">请输入姓名</p>
      </div>
      <div class="input-item male">
        <label>
          <input type="radio" name="male" value="0" v-model="studentObj.sex" />
          <div class="choose"></div>
          <span>男</span>
        </label>
        <label>
          <input type="radio" name="male" value="1" v-model="studentObj.sex" />
          <div class="choose"></div>
          <span>女</span>
        </label>
      </div>
      <div class="input-item" v-myblur>
        <input type="text" placeholder="邮箱" v-model="studentObj.email" />
        <p class="info">请输入邮箱</p>
      </div>
      <div class="input-item birth-year" :class="[birthInfo ? 'hasTip' : '']">
        <input type="text" placeholder="出生年" v-model="studentObj.birth" />
        <button class="birth-btn" @click="fillInBirth">获取出生年</button>
        <div class="error">愿走出半生，归来仍是少年</div>
      </div>
      <div class="input-item" v-myblur>
        <input type="text" placeholder="手机号" v-model="studentObj.phone" />
        <p class="info">请输入手机号码</p>
      </div>
      <div class="input-item" v-myblur>
        <input type="text" placeholder="住址" v-model="studentObj.address" />
        <p class="info">住址</p>
      </div>
      <div class="input-item">
        <button
          class="add-btn"
          :class="{ disabled: !addAbled }"
          @click="handleAdd"
        >
          添加
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "addStudent",
  data() {
    return {
      studentObj: {
        sNo: "",
        name: "",
        sex: "",
        birth: "",
        phone: "",
        address: "",
        email: "",
      },
      birthInfo: false,
    };
  },
  directives: {
    myblur: {
      inserted(el, binding) {
        let inputElement = el.querySelector("input");
        inputElement.addEventListener("blur", () => {
          if (inputElement.value === "") {
            el.classList.add("hasTip");
          } else if (el.classList.contains("hasTip")) {
            el.classList.remove("hasTip");
          }
        });
      },
    },
  },
  methods: {
    async handleAdd() {
      if (!this.addAbled) {
        return;
      }
      let {
        studentObj: params,
        studentObj: { sex },
      } = this;
      // 发送请求，添加一条学生信息
      try {
        const res = await this.$ajax.addStudent(params);
        if (res.status === "success") {
          let flag = confirm("添加成功，是否跳转至学生列表页？");
          flag && this.$router.push("/studentList");
          this.$store.commit("requestSure");
          this.clearForm();
        }
      } catch (error) {
        this.$toast({
          message: error,
          type: "error",
          duration: 2000,
        });
      }
    },
    fillInBirth() {
      this.birthInfo = true;
      this.studentObj.birth = new Date().getFullYear() - 18;
      setTimeout(() => {
        this.birthInfo = false;
      }, 3000);
    },
    clearForm() {
      let { studentObj } = this;
      Object.keys(studentObj).forEach((it) => (studentObj[it] = ""));
    },
  },
  computed: {
    addAbled() {
      let { studentObj } = this;
      return Object.keys(studentObj).every((it) => studentObj[it] !== "");
    },
  },
};
</script>

<style scoped>
.addForm {
  box-sizing: border-box;
  background-color: #fff;
  text-align: center;
  border-top: 1px solid #fff;
  height: 100%;
}
.title {
  margin: 20px 0 50px;
  height: 30px;
  border-bottom: 1px solid #ddd;
}
.title span {
  font-size: 30px;
  height: 56px;
  line-height: 56px;
  padding: 0 10px;
  background: #fff;
}
input,
button {
  outline: none;
  border: none;
  outline-offset: 0;
  transition: all 0.3s;
  color: #606266;
}
button {
  width: 100%;
  background: #00a1d6;
  border: 1px solid #00a1d6;
  color: #fff;
  font-size: 14px;
  cursor: pointer;
}
button:hover {
  background: #33b4de;
  border-color: #33b4de;
}
input::placeholder {
  color: #c0c4cc;
}
.formArea {
  width: 420px;
  margin: 0 auto;
}
.input-item {
  margin: 25px 0;
  position: relative;
}
.input-item:hover input[type="text"] {
  border-color: #c0c4cc;
}
.input-item input[type="text"] {
  height: 40px;
  line-height: 40px;
  width: 100%;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  text-indent: 1em;
}
.male {
  display: flex;
  align-items: center;
}
.male .choose {
  box-sizing: border-box;
  display: inline-block;
  height: 18px;
  width: 18px;
  border-radius: 50%;
  border: 1px solid #dcdfe6;
}
.male input[type="radio"] {
  display: none;
}
.male label {
  display: flex;
  align-items: center;
}
.male label + label {
  margin-left: 20px;
}
.male span {
  padding: 0 10px;
}
.male input[type="radio"]:checked ~ span {
  color: #f45d90;
}
.male input[type="radio"]:checked + .choose::after {
  content: "";
  display: block;
  width: 8px;
  height: 8px;
  margin-top: 50%;
  margin-left: 50%;
  transform: translateX(-50%) translateY(-50%);
  background: #33b4de;
  border-radius: 50%;
}
.male input[type="radio"]:checked + .choose {
  border-color: #33b4de;
}
.birth-year {
  position: relative;
}
.birth-btn {
  position: absolute;
  right: 1.5px;
  top: 1.5px;
  height: 36px;
  width: 130px;
  border-radius: 4px;
}
.add-btn {
  height: 40px;
  border-radius: 5px;
}
.add-btn.disabled {
  color: rgba(0, 0, 0, 0.25);
  background-color: #f5f5f5;
  border-color: #d9d9d9;
  cursor: not-allowed;
}
.info,
.error {
  display: none;
}
.hasTip .info,
.hasTip .error {
  font-size: 12px;
  color: chocolate;
  position: absolute;
  display: block;
  width: 240px;
  right: -260px;
  text-align: left;
  top: 50%;
  transform: translateY(-50%);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
