  display: none;
}

.form-item.error .info {
  display: block;
  font-size: 12px;
  color: #f45d90;
  position: absolute;
  width: 240px;
  right: -260px;
  top: 50%;
  transform: translateY(-50%);
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
} */
</style>