<template>
  <div class="maskIsShow">
    <div class="mask" @click.self="$emit('closeEdit')">
      <div class="dialog">
        <form action="#" class="editStudent">
          <div class="edit-sNo">
            <label for="edit-sNo">学号：</label>
            <input
              type="text"
              id="edit-sNo"
              name="edit-sNo"
              placeholder="请输入学号"
              v-model="studentInformation.sNo"
            />
          </div>
          <div class="edit-name">
            <label for="edit-name">姓名：</label>
            <input
              type="text"
              id="edit-name"
              name="edit-name"
              placeholder="请输入学生姓名"
              v-model="studentInformation.name"
            />
          </div>
          <div class="edit-email">
            <label for="edit-email">邮箱：</label>
            <input
              type="text"
              id="edit-email"
              name="edit-email"
              placeholder="请输入学生邮箱"
              v-model="studentInformation.email"
            />
          </div>
          <div class="edit-birth">
            <label for="edit-birth">出生年：</label>
            <input
              type="text"
              id="edit-birth"
              name="edit-birth"
              placeholder="请输入学生出生年"
              v-model="studentInformation.birth"
            />
          </div>
          <div class="edit-phone">
            <label for="edit-phone">手机号：</label>
            <input
              type="tel"
              id="edit-phone"
              name="edit-phone"
              placeholder="请输入学生手机号"
              v-model="studentInformation.phone"
            />
          </div>
          <div class="edit-address">
            <label for="edit-address">住址：</label>
            <input
              type="text"
              id="edit-address"
              name="edit-address"
              placeholder="请输入学生住址"
              v-model="studentInformation.address"
            />
          </div>
          <div class="edit-btn">
            <button id="editBtn" @click.prevent="submit">确定</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "editStudent",
  props: {
    student: {
      type: Object,
    },
  },
  computed: {
    studentInformation() {
      return JSON.parse(JSON.stringify(this.student));
    },
  },
  created() {
  },
  methods: {
    // async submit(){
    //   let res = await this.$ajax.editStudent(this.studentInformation);
    //   if(res.status === 200){
    //     alert("修改成功")
    //   }
    // }
    submit(){
      this.$emit("submitChange",this.studentInformation);
    }
  },
};
</script>

<style scoped>
.maskIsShow {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 99;
}

.mask {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mask .dialog {
  width: 30vw;
  min-width: 300px;
  height: 50vh;
  min-height: 400px;
  background-color: #ccc;
  border-radius: 10px;
}

.mask .dialog form {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 30px 20px 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mask .dialog form > div {
  width: 100%;
  box-sizing: border-box;
  padding: 0 30px 0 20px;
  margin: 15px 0px;
  height: 26px;
  display: flex;
  align-items: center;
}

.mask .dialog form > div input {
  height: 22px;
  border: 1px solid #eee;
  outline: none;
  flex-grow: 1;
  border-radius: 4px;
  padding-left: 10px;
  font-size: 12px;
}

.mask .dialog form > div label {
  line-height: 26px;
  box-sizing: border-box;
  padding-right: 4px;
  width: 120px;
  text-align: right;
}

.mask .dialog form > div:last-child {
  height: 32px;
  justify-content: flex-end;
}

.mask .dialog form > div button {
  height: 100%;
  width: 55px;
  border: 0;
  outline: none;
  align-self: flex-end;
  background-color: #409eff;
  color: #fff;
  border-radius: 12px;
  justify-self: end;
  cursor: pointer;
}
</style>
