<template>
  <div>
    <div class="username formitem">
      <div>
        <label for="username">用户名：</label>
        <input
          type="text"
          id="username"
          placeholder="用户名"
          v-model="logonData.username"
        />
      </div>
    </div>
    <div class="account formitem">
      <div>
        <label for="account">账号：</label>
        <input
          type="text"
          id="account"
          placeholder="账号"
          v-model="logonData.account"
        />
      </div>
    </div>
    <div class="password formitem">
      <div>
        <label for="password">密码：</label>
        <input
          type="password"
          id="password"
          placeholder="输入密码"
          v-model="logonData.password"
        />
      </div>
    </div>
    <div class="rePassword formitem">
      <div>
        <label for="rePassword">确认密码：</label>
        <input
          type="password"
          id="rePassword"
          placeholder="请确认密码"
          v-model="logonData.rePassword"
        />
      </div>
    </div>
    <div class="btns">
      <button type="button" @click.prevent="reset">返回至登录</button>
      <button type="button" @click.prevent="registor">注册</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      logonData: {
        username: "",
        password: "",
        account: "",
        rePassword: "",
      },
    };
  },
  methods: {
    reset() {
      for (let i in this.logonData) {
        this.logonData[i] = "";
      }
      this.$router.push("/login");
    },
    async registor() {
      let data = this.logonData;
      if (
        data.username !== "" &&
        data.password !== "" &&
        data.account !== "" &&
        data.rePassword !== ""
      ) {
        if (data.password !== data.rePassword) {
          this.$toast({
            message: "两次密码输入不一致",
            type: "error",
            duration: 2000,
          });
        } else {
          try {
            let res = await this.$ajax.logon(this.logonData);
            this.$MessageBox("注册成功，是否跳转登录页？")
              .then(() => {
                this.$toast({
                  message: "注册成功",
                  type: "success",
                });
                this.$router.push("/login");
              })
              .catch(() => {});
          } catch (err) {
            this.$toast({
              message: err,
              type: "error",
              duration: 2000,
            });
          }
        }
      } else {
        this.$toast({
          message: "请完善注册信息",
          type: "error",
          duration: 1500,
        });
      }
    },
  },
};
</script>

<style scoped>
@import "~@/assets/style/loginCommon.css";

.rePassword {
  margin-bottom: 30px;
}
</style>